<template>
    <div>
        <span class="title">精品歌单</span>
        <div class="play" v-for="item in playlist.length" :key="item.id">
          <router-link class="playlist" :to="{path:'/playdetails',query:{id:playlist[item-1].id}}">
            <img v-lazy="playlist[item-1].coverImgUrl" alt="">
            <div class="playlist_span">
              <span>{{playlist[item-1].name}}</span>
            </div>
          </router-link>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      playlist: []
    }
  },
  created() {
    this._getPlaylist()
  },
  methods: {
    _getPlaylist() {
      axios.get(process.env.API_HOST + '/songList/highQuality?cat=全部&pageSize=100').then(res => {
        // console.log(res)
        const playlists = res.data.data
        this.playlist = playlists
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style scoped>
.title {
  font-size: 1.2rem;
  color: rgb(6, 177, 177);
}
.play {
width: 100%;
}
.playlist {
width: 50%;
float: left;
margin: 5px 0;
}
.playlist img {
width: 90%;
margin: 0 auto;
height: 24vh;
}
.playlist span {
font-size: .9rem;
}
.playlist_span {
width: 90%;
margin: 0 auto;
text-align: left;
line-height: 20px;
color: cornflowerblue;
}
a {
  text-decoration: none;
}
</style>



